@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500&display=swap');

@mixin linkStyle() {
  text-decoration: none;
  color: #000000;
}

@mixin linkHover() {
  color: #778899;
  cursor: pointer;
}

@mixin buttonHover() {
  cursor: pointer;
  background-color: #f0f8ff;
}

* {
  color: #303030;
}

html {
  min-width: 100%;
  min-height: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  body {
    font-family: 'Montserrat', sans-serif;
    margin-left: 0;
    min-height: 100vh;
    margin-bottom: 8em;
    overflow-x: hidden;

    &.no-scroll {
      overflow-y: hidden;
    }

    a {
      @include linkStyle();
    }

    /* HEADER */
    header {
      background: whitesmoke;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 2.5em;
      padding: 1.5em;
      margin-bottom: 10em;
      z-index: 5;

      .container {
        height: 3em;
        display: flex;
        justify-content: space-around;
        padding-bottom: 2em;

        h1 {
          margin-top: -5px;
          margin-left: -5px;
          font-size: 3rem;
        }

        nav {
          ul {
            display: flex;

            li {
              margin-right: 3em;
              font-size: 1rem;
              list-style-type: none;
            }
          }

          li a:hover {
            @include linkHover();
          }
        }

        .header-util i {
          font-size: 0.8rem;

          &:hover {
            @include linkHover();
          }
        }

        /* Highlight current page link */
        .current-menu-item a {
          text-shadow: 1px #ff0000;
          color: lightgray;
        }
      }
    }

    /* MAIN */
    main {
      display: flex;
      flex-direction: column;

      .heading-section {
        background-image: url('../images/image-front.jpg');
        background-size: cover;
        background-position: center;
        background-attachment: fixed;
        height: 50em;

        .heading {
          h2 {
            font-size: 3rem;
            color: white;
          }

          h3 {
            color: white;
          }

          color: whitesmoke;
          text-align: center;
          padding: 2em 2em;
          margin: 0 auto;
          margin-top: 15em;
          background-color: black;
          opacity: 0.75;
          width: 40%;
          transition: all 0.2s ease-in;

          &:hover {
            opacity: 0.9;
            background-color: #282828;
            cursor: pointer;
          }
        }
      }

      .show-posts {
        display: flex;

        h2 {
          text-align: center;
          font-size: 2rem;
        }
        hr {
          margin-bottom: 2em;
        }
        .post-title-style {
          font-size: 1.2rem;
          list-style-type: none;
          text-align: center;
        }

        .workout-posts {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          padding: 1em;
          background-color: white;
          border-right: 3px solid gray;

          button {
            font-size: 1rem;
            margin-top: 2em;
            border-radius: 5px;
            border: none;
            background-color: lightgray;
            padding: 1em;
            transition: all 0.2s ease-in;

            &:hover {
              cursor: pointer;
              background-color: whitesmoke;
            }
          }
        }
      }

      .homepage-classes {
        text-align: center;
        margin-top: 5em;

        h2 {
          font-size: 2.5rem;
          margin-bottom: 3em;
        }

        .classes-container {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          margin: 5em;
          gap: 6em;

          .single-class h3 {
            position: relative;
            top: 40%;
            width: 50%;
            margin: 0 auto;
            font-size: 2.3rem;
            font-weight: bolder;
            padding: 0.5em;
            color: white;
          }

          .yoga {
            background-image: url('../images/yoga.jpg');
            background-size: cover;
            background-position: center;
            height: 25em;
            width: 45%;
            transition: all 0.2s ease-in;
            opacity: 0.7;

            &:hover {
              cursor: pointer;
              opacity: 0.8;
            }
          }

          .zumba {
            background-image: url('../images/zumba.jpg');
            background-size: cover;
            background-position: center;
            height: 25em;
            width: 45%;
            transition: all 0.2s ease-in;
            opacity: 0.7;

            &:hover {
              cursor: pointer;
              opacity: 0.8;
            }
          }

          .outdoor {
            background-image: url('../images/outdoor.jpg');
            background-size: cover;
            background-position: center;
            height: 25em;
            width: 45%;
            transition: all 0.2s ease-in;
            opacity: 0.7;

            &:hover {
              cursor: pointer;
              opacity: 0.8;
            }
          }

          .cycling {
            background-image: url('../images/cycling.jpg');
            background-size: cover;
            background-position: center;
            height: 25em;
            width: 45%;
            transition: all 0.2s ease-in;
            opacity: 0.7;

            &:hover {
              cursor: pointer;
              opacity: 0.8;
            }
          }
        }
      }

      .history-section {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        h2 {
          font-size: 2.5rem;
        }

        .our-history {
          text-align: center;
          display: flex;
          margin: 5em;
          justify-content: center;
          gap: 2em;

          img {
            width: 40%;
            height: 30%;
          }
        }
      }

      /* EVENTS */
      .homepage-events {
        margin-top: 5em;

        .events-container {
          display: flex;
          margin: 0 auto;
          background-image: url('../images/events.jpg');
          background-size: cover;
          background-position: center;
          height: 40em;
          width: 100vw;
          transition: all 0.2s ease-in;

          a {
            position: relative;
            top: 40%;
            width: 10em;
            height: 5%;
            text-align: center;
            margin: 0 auto;
            padding: 1.2em;
            padding-top: 1em;
            font-size: 2rem;
            background-color: #778899;
            opacity: 0.8;
            color: white;
            transition: all 0.3s ease-in;

            &:hover {
              cursor: pointer;
              background-color: lightblue;
            }
          }
        }
      }
    }

    .blog-section {
      h1 {
        font-size: 2.5rem;
      }
    }

    /* CONTACT PAGE */
    .form-container {
      margin: 6em;
      display: flex;
      flex-direction: column;
      justify-self: center;
      align-items: center;

      h1 {
        font-size: 2.5rem;
      }

      .contact-form {
        display: flex;
        flex-direction: column;
        width: 60%;

        button {
          margin: 0.5em;
          padding: 1em;
          border-radius: 5px;
          border: none;
          font-weight: bold;
          height: 3.5em;
          transition: all 0.2s ease-in;

          &:hover {
            @include buttonHover();
          }

          &:active {
            transform: translateY(-2px);
          }
        }

        input,
        textarea {
          font-family: 'Montserrat', sans-serif;
          border-top: none;
          border-radius: 5px;
          margin: 0.5em;
          padding: 1em;
        }
      }
    }

    .page-container {
      margin-bottom: 5em;

      .generic-content {
        margin-top: 5em;
        display: flex;
        gap: 2em;
      }

      .link-list {
        li {
          list-style-type: none;

          a {
            position: relative;
            display: inline-block;

            .related-post-container {
              width: 30em;
              height: 22em;

              img {
                display: block;
                width: 30em;
                height: 22em;
              }

              .text-overlay {
                position: absolute;
                text-align: center;
                bottom: 0;
                background-color: #f0f8ff;
                width: 100%;
                height: 20%;
                opacity: 0;
                transition: all 0.2s ease-in;

                h3 {
                  font-size: 1rem;
                  padding-top: 0.5em;
                }
              }
            }

            &:hover {
              cursor: pointer;

              .text-overlay {
                opacity: 0.9;
              }
            }
          }
        }
      }

      .single-event {
        display: flex;
        gap: 2em;

        .date-container {
          background: darkblue;
          margin-top: 0.5em;
          height: 4em;
          width: 4em;
          border-radius: 50%;
          padding: 1em;

          p {
            text-align: center;
            color: white;
            font-weight: bolder;
            font-size: 0.8rem;
            height: 4em;
            width: 4em;
          }
        }
      }
    }

    .page-banner {
      margin-top: 8em;
      h1 {
        font-size: 3rem;
      }
    }
    .generic-content {
      /* SEARCH FORM WITH JS DISABLED */
      .search-form-container {
        .search-form {
          margin: 0 auto;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          gap: 1em;
          background-color: #778899;
          width: 50%;
          padding: 1em;
          border: none;
          border-radius: 4px;

          label {
            font-size: 1.5rem;
          }

          .search-form-inputs {
            input {
              padding: 0.8em;
              width: 100%;
              border: none;
              border-radius: 4px;
              margin: 0.2em;
            }

            .search-input-submit:hover {
              @include buttonHover();
            }
          }
        }
      }
    }
  }

  .class-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 1em;

    a {
      text-align: center;
      font-size: 2.3rem;
      font-weight: bolder;
      padding: 0.5em;
      color: black;
    }

    .yoga {
      background-image: url('../images/yoga.jpg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 60em;
      height: 60em;
      transition: all 0.2s ease-in;
      opacity: 0.7;

      &:hover {
        cursor: pointer;
        opacity: 0.8;
      }
    }

    .zumba {
      background-image: url('../images/zumba.jpg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 60em;
      height: 60em;
      transition: all 0.2s ease-in;
      opacity: 0.7;

      &:hover {
        cursor: pointer;
        opacity: 0.8;
      }
    }

    .outdoor {
      background-image: url('../images/outdoor-workout.jpg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 60em;
      height: 60em;
      transition: all 0.2s ease-in;
      opacity: 0.7;

      &:hover {
        cursor: pointer;
        opacity: 0.8;
      }
    }

    .cycling {
      background-image: url('../images/cycling.jpg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 60em;
      height: 60em;
      transition: all 0.2s ease-in;
      opacity: 0.7;

      &:hover {
        cursor: pointer;
        opacity: 0.8;
      }
    }

    &:hover {
      cursor: pointer;

      .text-overlay-class {
        opacity: 0.9;
      }
    }
  }

  /* MAP */
  .mapp-layout {
    position: relative;
    z-index: 1;
  }

  /* WORKOUTS PAGE */
  .posts-container {
    margin: 8em;

    h1 {
      text-align: center;
      font-size: 2.5rem;
    }
  }

  .related-post {
    margin: 3em;
  }

  /* ABOUT PAGE */

  /* FOOTER */
  footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 5.5em;
    padding: 1em;
    background: whitesmoke;
    position: relative;
    top: calc(100vh - 100px);
    left: 0;
    bottom: 0;

    i {
      margin: 0.2em;
    }

    .fa-facebook-official {
      color: #00008b;
      font-size: 2rem;
    }

    .fa-instagram {
      color: red;
      font-size: 2rem;
    }

    .fa-twitter {
      color: lightblue;
      font-size: 2rem;
    }
  }

