  /* SEARCH OVERLAY */
  .search-overlay {
    background: rgb(224, 224, 224);
    box-sizing: border-box;
    visibility: hidden;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.98;
    min-height: 100%;

    &--active {
      display: block;
      visibility: visible;
    }

    .search-overlay-top {
      background-color: #778899;
      height: 5em;
      position: relative;
      top: -2;
      width: 100%;

      .search-container {
        display: flex;
        justify-content: center;
        gap: 1em;
        padding: 1em;

        .search-input {
          width: 75%;
          box-sizing: border-box;
          border: none;
          background: transparent;
          font-size: 1.8rem;
          font-weight: 300;
          color: #000000;
          outline: none;
          padding-left: 1.2em;

          &::placeholder {
            font-size: 1.8rem;
            color: #000000;
          }
        }

        .fa-search {
          margin-top: 0.5em;
          margin-right: -2em;
          font-size: 1.2rem;
          color: #000000;
        }

        .fa-window-close {
          font-size: 1.5rem;
          color: #000000;

          &:hover {
            cursor: pointer;
          }
        }
      }
    }

    .results-container {
      overflow-y: auto;
      padding: 2em;
      margin-top: -2em;
      height: 100%;
      overflow-y: scroll;

      .row {
        display: flex;
        justify-content: space-around;

        .one-third {
          display: flex;
          flex-direction: column;

          a {
            text-decoration: underline;
          }

          h2 {
            font-size: 2rem;
            text-align: center;
          }

          p {
            text-align: center;
          }

          li {
            list-style-type: none;
            margin: 0.5em;
          }

          /* Style classes and workouts search list */
          .search-list {
            display: flex;
            flex-direction: row;
            justify-content: center;

            li {
              transition: transform 0.2s ease-in;

              &:hover {
                transform: scale(1.1);
              }

              /* Text overlay on image */
              .image-link {
                text-decoration: none;
                position: relative;
                text-align: center;

                img {
                  height: 80%;
                  width: 80%;
                }

                span {
                  width: 100%;
                  background-color: lightgray;
                  color: #000000;
                  position: absolute;
                  top: 50%;
                  left: 50%;
                  transform: translate(-50%, -50%);
                  padding-top: 0.5em;
                  padding-bottom: 0.5em;
                }
              }
            }
          }
        }

        .single-event {
          display: flex;
          gap: 2em;

          .date-container {
            background: darkblue;
            margin-top: 0.5em;
            height: 4em;
            width: 4em;
            border-radius: 50%;
            padding: 1em;

            p {
              text-align: center;
              padding: 0.5em 0.5em;
              color: white;
              font-weight: bolder;
              font-size: 0.8rem;
              height: 4em;
              width: 4em;
            }
          }
        }
      }
    }
  }

  /* Loading Spinner */
  @keyframes spinner {
    from {
      transform: translate3d(-50%, -50%, 0) rotate(0deg);
    }
    to {
      transform: translate3d(-50%, -50%, 0) rotate(360deg);
    }
  }

  .spinner {
    height: 100vh;
    position: relative;

    &::before {
      animation: 2s linear infinite spinner;
      border: solid 4px #00008b;
      border-bottom-color: #f0f8ff;
      border-radius: 50%;
      content: '';
      height: 3em;
      width: 3em;
      position: absolute;
      top: 10%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
    }
  }

  .blog-section {
    margin-top: 7em;
    text-align: center;
  }

  .page-container {
    margin: 7em;
  }
}

